@import url(https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css);
.login_filed {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.login_div {
  width: 300px;
  padding: 5px;
  display: flex;
  flex-direction: column;
  border: 1px solid black;
}

.login_form {
  display: grid;
  grid-template-rows: 50% 50%;
  grid-template-columns: 75% 25%;
  margin-bottom: 5px;
}
.login_id {
  grid-row: 1/2;
  grid-column: 1/2;
  border: 1px solid black;
  padding: 5px;
  margin-bottom: 2.5px;
}
.login_pw {
  grid-row: 2/3;
  grid-column: 1/2;
  border: 1px solid black;
  padding: 5px;
  margin-top: 2.5px;
}
.login_submit {
  text-align: center;
  grid-row: 1/-1;
  grid-column: 2/3;
  border: 1px solid black;
  margin-left: 5px;
}
.login_error {
  color: red;
  border: 1px solid black;
  margin-bottom: 5px;
  padding: 3px;
}
.changeAccount {
  border: 1px solid black;
  margin-bottom: 5px;
  height: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.atherBtn {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 5px;
  gap: 5px;
}
.login_google {
  height: 24px;
}

.flag_box {
  display: flex;
  align-items: center;
  padding-bottom: 10px;
}

.flag_month_day {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
}

.fb_time {
}

.fb_name {
  width: 100px;
  text-align: center;
}

.fb_btn {
  padding-left: 30px;
}

.flagSearch {
  width: 100%;
  min-height: 200px;
}
.fs_header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 3px;
  padding-right: 3px;
  margin-bottom: 5px;
}
.fs_menu {
  cursor: pointer;
}
.search_box {
  width: 100px;
}
.userList {
  border: 1px solid black;
  margin: 3px;
  padding: 3px;
  cursor: pointer;
}
.selectedUser {
  text-align: center;
}

.notice {
  border: 1px solid black;
  margin: 5px;
  width: 80%;
  padding: 3px;
}

.notice_header {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  height: 24px;
  padding: 3px;
}

.notice_title_and_button {
  display: flex;
}

.notice_title {
}

.notice_contents {
  border-top: 1px solid black;
  padding: 3px;
}

textarea {
  width: 100%;
}

.notice_form {
  display: flex;
  flex-direction: column;
  border: 1px solid black;
  padding: 10px;
  margin: 10px;
  margin-bottom: 0px;
  width: 80%;
}

.notice_form_title {
  margin-bottom: 10px;
}

.notice_form_contents {
  margin-bottom: 10px;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  width: 200px;
  background-color: #f1f1f1;
}

li a {
  display: block;
  color: #000;
  padding: 8px 16px;
  text-decoration: none;
}

/* Change the link color on hover */
li a:hover {
  background-color: rgb(67, 187, 107);
  color: white;
}

* {
  box-sizing: border-box;
}

input {
  box-sizing: border-box;
  /* all: unset;
  appearance: none; */
}

body {
  /* background-color: black; */
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 14px;
  /* color: white; */
  background: #ffeef8;
}

a {
  text-decoration: none;
  color: inherit;
}

h1 {
  font-weight: 700;
}

.contents {
  max-width: 890px;
  width: 100%;
  height: 100vh;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title {
  margin: 10px;
}

